@fontes: "font/gilroy";
@images: "../img";

@font_padrao:Gilroy-Regular, Lato, sans-serif;
@font-bold: Gilroy-Bold, Lato, sans-serif;
@font-black: Gilroy-Black, Lato, sans-serif;
@font-medium: Gilroy-Medium, Lato, sans-serif;

@margin-defalt: 80px;
@white:#f3f3f3; 

@font-face {
  src: url('@{fontes}/Gilroy-Regular.ttf') ;
  font-family: Gilroy-Regular;
  font-weight: normal;
};
@font-face {
  src: url("@{fontes}/Gilroy-Black.ttf") ;
  font-family: Gilroy-Black;
  font-weight: normal;
};
@font-face {
  src: url('@{fontes}/Gilroy-Bold.ttf') ;
  font-family: Gilroy-Bold;
  font-weight: normal;
};
@font-face {
  src: url('@{fontes}/Gilroy-Medium.ttf') ;
  font-family: Gilroy-Medium;
};
@media screen and (min-width:1090px) {
  nav ul a{
    font-size: 16px;
  }
}
@media screen and (min-width:1020px) {
  div.hero-foto{
    height: 700px;
  }
}
@media screen and (max-width:895px) {
  div.hero-foto{
    height: 336px;
  }
}
@media screen and (max-width:768px) {
  input.header-search{
    width: 52px;
    padding: 0;
    position: static;
    /*height: 52px;*/
    border-radius: 52px;
    background-position: center center;
    transition: width 60ms ease-out;
    &:focus{
      width: 59%;
      left: 23%;
      position: absolute;
      background-position: 13px center;
      padding: 13px 13px 13px 43px;
      font-size: 20px;
      box-sizing: border-box;
      transition: width 600ms;
    }
    &::placeholder{
      color: transparent;
    }
  }
  div.logo{
    flex-grow: 0;
  }
  div.search-div{
    margin-right: 0;
  }
  div.container.container-flex{
    justify-content: space-between;
  }
  nav.nav{
    width: 44%;
  }
  nav ul a{
    font-size: 9px;
  }
  header .container{
    margin: 0 15px;
  }
  section.section-hero{
    margin-left: 15px;
    margin-right: 15px;
  }
  main .hero-text h1{
    font-size: 62px;
  }
  .hero-foto:hover img.img__header-svg {
    left: 50%;
  }
  div.hero-text{
    width: 50%;
    padding-right: 0;
    margin-right: 0;
  }
  div.hero-foto{
    width: 49%;
    height: 325px;
  }
  aside.aside-1{
    margin-left: 0px;
    margin-right: 0px;
  }
  .aside-1 button.addToCar{
    width: 40%;
    box-sizing: content-box;
  }
  section.section-2{
    margin: 80px 15px;
  }
  div.section-2__content> h3{
    font-size: 2.5em;
  }
  div.section-2__content> p{
    font-size: 0.9em;
  }
  div.img-section2{
    display: flex;
    vertical-align: middle;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: flex-end;
  }
  div.img-section2{
    width: 80%;
  }
  .invert> .section-2__content{
    width: 57%;
  }
  aside.section-runner{
    height: 413px;
    background: url('@{images}/runner-md.jpg');
    span{
      font-size: 3.5em;
      padding-top: 40px;
      padding-left: 19px;
    }
  }
  aside.aside-contato{
    margin: 150px 0;
  }
  div.div-map{
    min-width: 0  ;
    width: 50%;
  }
  form.form{
    width: 50%;
  }
  label.checkbox-label{
    font-size: 14px;
  }
  img.footer__logo{
    width: 64px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .logo__footer--fix{
    width: 88%;
  }
  div.footer-links{
    flex-wrap: wrap;
    &>div:nth-child(2),div.footer-links>div:nth-child(3){
      width: 50%;
    }
    div h5{
      font-family: @font_padrao;
      font-size: 20px;
    }
    &>div:last-child{
      display: none;
    }
    a{
      font-size: 18px;
      font-family: @font_padrao;
      font-weight: 400;
      line-height: 35px;
    }
  }
  div.footer-links>div:nth-child(3){
    width: 42%;
  }
  div.authors-bar {
    p{
      font-size: 1.2em;
    }
    strong{
      font-family: @font-black;
    }
  }
}
@media screen and (max-width:630px) {//bug de scroll
  body>header{
    .menu{
      display: block;
    }
    .nav{
      display: none;
    }
    .search-div{
      flex-grow: 1;
      justify-content: center;
    }
  }
  body > main{
    aside.aside-contato{
      div.div-map{
        display: none;
      }
      form.form{
        width: 100%;
      }
    }
    aside.aside-1{
      img{
        width: 88%;
      }
    }
  } 
}
@media screen and (max-width:531px){
  body>aside.section-runner{
      span{
        font-size: 2.5rem;
      }
  }
  body > header{
      .bag-btn{
        display: none;
      }
    }
    .section-hero{
      flex-direction: column-reverse;
      
      .hero-foto{
        width: 37%;
        height: 262px;
        img{
          left:unset;
        }
        .img__header-tenis{
          width: 46%;
        }
        .img__header-svg{
          width: 57%;
        }
        
        &:hover .img__header-tenis,&:hover .img__header-svg{
          left: unset;
        }
      }
      .hero-text{
        width: 100%;
        align-items: center;
      }
    }
    .section-2> div{
      flex-direction: column;
      .img-section2,p,.section-2__content{
        width: 100%;
      }
    }
    
    aside.aside-contato{
      margin: 50px 0;
      div.div-map{
        display: none;
      }
      form.form{
        width: 100%;
        input{
          font-size: 1rem;
          height: 6%;
        }
        button.send{
          font-size: 1em;
          padding: 0;
          height: 6%;
        }
      }
    }
  }

body{
  margin: 0;
}
header{
  padding: 29px 0;
  font-family: @font_padrao;
  font-weight: 500;
  color: black;
}
.container-flex{
  display: flex;
}
.container-flex-spaced{
  justify-content: space-between;
  align-items: center;
}
.container {
  max-width: 100%;
  margin: 0 @margin-defalt;
}
.logo{
  justify-self: left;
  flex-grow: 2;
}
a{
  text-decoration: none;
  font-weight: 700;
  color: black;
}
nav{
  flex-shrink: 2;
  width: 50%;
  ul{
    align-items: center;
    list-style-type: none;
    display: flex;
    padding-left: 0;
    flex-direction: row;
    li{
      margin: 0 3%;
      font-size: 0.6em;
    }
  }
}
.search-div{
  flex-grow: 0;
  display: flex;
  justify-content: end;
  align-items: center;
  margin-right: 10px;
  .header-search{
    background-image: url('@{images}/Vectorlupa.svg');
    background-repeat: no-repeat;
    background-position: 13px center;
    background-color: #f2f2f2;
    border-radius:50px;
    padding: 13px 13px 13px 43px;
    border:1px solid #f2f2f2;
    width: 65%;
  }
}
.bag-btn{
  cursor: pointer;
  border:none;
  background-repeat: no-repeat;
  background:url('@{images}/Header/Bagbag.svg');
  height: 52px;
  width: 52px;
  flex-shrink: 0;
}
.menu{
  background:url('@{images}/phone_images/burguer.svg') center;
  cursor: pointer;
  border: none;
  background-repeat: no-repeat;
  height: 52px;
  width: 52px;
  flex-shrink: 3;
  display: none;
}
.section-hero{
  margin-top: 40px;
  margin-right: @margin-defalt;
  margin-left: @margin-defalt;
  display: flex;
}
.hero-foto{
  flex-grow: 4;
  text-align: center;
  height: 554px;
  &:hover  {
    .img__header-svg{
      transition: all 1s cubic-bezier(0.18, 1.13, 1, 0.99);
      left: 51%;
    }
    .img__header-tenis{
      transition: all 1s cubic-bezier(0.18, 1.13, 1, 0.99);
      left: 65%;
    }
  }
}
.img__header{
  &-svg{
    width: 35%;
    left: 54%;
    position: absolute;
    transition: left 2s ease;
  }
  &-tenis{
    width: 29%;
    transition: left 2s;
    position: absolute;
    top: 165px;
    left: 60%;
  }
}
.hero-text{
  display: flex;
  width: 50%;
  flex-direction: column;
  align-items: flex-start;
  padding-right: 15px;
  margin-right: 6%;
  h1{
    font-family: @font-bold;
    color: #9F9F9F;
    font-size: 6em;
    margin: 0 0 10px 0;
    background-image: linear-gradient(270.06deg, #DADADA 20.97%, #9F9F9F 99.92%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  p{
    font-family: @font_padrao;
    font-weight: 300;
    font-size: 1.3em;
    text-align: justify;
  }
}
button.btn-hero{
  width: calc(60% + 84px );
  height: 16%;
  font-size: 1.2em;
  max-width: 290px;
}
.addToCar{
  font-family: @font-bold;
  background-image: url('@{images}/Vector.svg');
  background-position: 90% 50%;
  background-repeat: no-repeat;
  margin-top:10%;
  border: 2px solid black;
  border-radius: 12px;
  text-align: start;
  padding: 20px 30px 20px 21px;
  font-weight: 700;
  font-size: 0.8em;
  background-color: transparent;
  vertical-align: middle;
  max-width: 50%;
  min-width: 220px;
  box-sizing: border-box;
  transition: border 0.5s, background-color 0.8s;
  vertical-align: middle;
  &:hover{
    background-color: @white;
    border-radius: 12px;
    border: 1px solid transparent;
  }
  &:active{
    background-color: #D2D2D2;
    transition: background-color 100ms;
  }
  img{
    margin-left: 4px;
    vertical-align: middle;
  }
}
.aside-1{
  /*width: 95%;*/
  margin-top: 40px;
  margin-left: @margin-defalt;
  margin-right:@margin-defalt;
  background: @white;
  border-radius: 8px;
  text-align: center;
  padding: 40px;
  h3{
    font-weight: 700;
    font-family: Gilroy-Black,lato,sans-serif;
    font-size: 2em;
    text-transform: uppercase;
  }
  p{
    font-weight: 700;
    font-size: 1.4em;
    font-family: Gilroy-Bold,lato,sans-serif;
  }
  & > div{
    margin: auto;
    display:flex;
    flex-direction:column;
    align-items: center;
  }
  .addToCar{
    margin-top: 5%;
    height: 16%;
    font-size: 1.2em;
    width: 18%;
    background-image: none;
    text-align: center;
  }
}
.bolinha(){
  opacity: 0.5;
  border: 2px solid #FFFFFF;
}
.bolinha{
  height: 30px;
  width: 30px;
  border-radius: 50px;
  margin:0 5px;
  &-div{
    display: flex;
    flex-direction: row;
    justify-content: center;

  }
  &:nth-child(1){
    background: linear-gradient(270.06deg, #E4E4E4 20.97%, #FFFFFF 99.92%);
    border: 2px solid rgba(31, 31, 31, 0.5);
  }
  &:nth-child(2){
    background: linear-gradient(277.74deg, #FFE08D 9.16%, #FFB757 91.03%);
    .bolinha()
  }
  &:nth-child(3){
    background: linear-gradient(270.06deg, #464646 20.97%, #D3D3D3 99.92%);
    .bolinha()
  }
  &:nth-child(4){
    background: linear-gradient(270.06deg, #464646 20.97%, #D3D3D3 99.92%);
    .bolinha()
  }
}
.section-2{
  margin: 70px @margin-defalt;
  &>div{
    display: flex;
    margin: @margin-defalt 0;
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  h3{
  font-family: @font-black;
  color: #9F9F9F;
  font-size: 4em;
  font-weight: bolder;
  margin-top: 0;
  margin-bottom: 10px;
  text-transform: capitalize;
  background-image: linear-gradient(270.06deg, #DADADA 20.97%, #9F9F9F 99.92%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  }
  p{
    font-family: @font_padrao;
    width: 80%;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    text-align: left;
  }
  &__content{
    width: 561px;  
  }
  &>div>div{
    vertical-align: middle;
  }
}
div.invert{
  flex-direction: row-reverse;
  p{
    overflow: hidden;
    margin-left: 2%;
    
  }
  h3{
    overflow: hidden;
    margin-left: 2%;
    
}
}
.img-section2 img{
  width: 100%;
  height: auto;
}
.section-runner{
  background-image:url('@{images}/corredor.jpg') ;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  width: 100%;
  height: 720px;
  span{
    font-size: 6.5em;
    font-weight: 700;
    text-transform: uppercase;
    font-family: @font-bold;
    display: block;
    line-height: 140%;
  }
}
.aside-contato{
  margin: 150px @margin-defalt;
}
.div-map{
  background-repeat: no-repeat;
  background-image: url('@{images}/map.png');
  background-size: cover;
  height: 56em;
  float: left;
  min-height: 500px;
  width: 60%;

}
.form{
  color: #1F1F1F;
    background-color: @white;
    padding-left: 2%;
    padding-right: 2%;
    display: flex;
    padding-top: 2%;
    padding-bottom: 2%;
    height: 56em;
    width: 35%;
    box-sizing: border-box;
    font-family: @font-medium;
    border-radius: 0 8px 8px 0;
    flex-direction: column;
    justify-content: space-evenly;
    h3{
      font-size: 2em;
      margin-top: 0;
      font-family:@font-black;
    }
    input,textarea{
      box-sizing: border-box;
      font-size: 1.4em;
      background: #FFFFFF;
      border-radius: 12px;
      border: none;
      display: block;
      padding: 24px 28px;
      width: 100%;
      height: 8%;
      margin-bottom: 20px;
    }
    &> textarea{
      resize: none;
      height: 35%;
      outline: none;
      font: 1.4em @font_padrao;
    }
}
input,button{
  outline: none;
}
.checkbox-label{
  display: block;
  position: relative;
  padding-left: 20px;
  font-size: 12px;
  font-weight: 400;
  font-size: 1.3em;
  font-family: @font_padrao;
  color: #969696;
}
.check{
  position: absolute;
  opacity: 0;
  height: 17px;
  width: 17px;
  left: 0;
  bottom: 7;
  z-index: 2;
  /*background: #FFFFFF;
  border: 2px solid #D2D2D2;
  border-radius: 4px;*/
}
.chk-span{
  position: absolute;
  top: 0;
  left: 0;
  height: 17px;
  width: 17px;
  background-color: white;
  border: 1px solid gray;
  border-radius: 2px;
}
.check:checked ~ .chk-span{
  background-color: #002D32;
  border: none;
}
.chk-span::after{
  content: '';
  position: absolute;
}
.label-div .chk-span::after{
   content: '';
   left: 5px;
   top: 1px;
   width: 5px;
   height: 9px;
   transform: rotate(45deg);
   border-right: 3px solid #fff;
   border-bottom: 3px solid #fff;

}
.label-div .check:checked ~ .chk-span::after{
  display: block;
}
.label-div{
  margin-top: 20px;
  margin-bottom: 15px;
}
button.send{
  font-family: @font-bold;
  margin-top:10%;
  padding: 20px;
  font-weight: 700;
  background-color: transparent;
  text-align: center;
  width: 100%;
  border: 2px solid #1F1F1F;
  border-radius: 12px;
  font-size: 1.4em;
}
.footer{
  background-color: #F3F3F3;
  margin: 0;
  text-align: center ;
  margin-top: 9%;
  &-links{
    background-color: @white;
    margin: 0;
    display: flex;
    gap:16px;
    justify-content: space-between;
    margin: 40px 0px 0 0 ;
    padding: 0 3%;
    font-family: @font_padrao;
    ul{
      list-style-type: none;
      padding-left: 0;
    }
    a{
      font-size: 1.6em;
      font-family:@font_padrao;
      font-weight: 400;
    }
    div{
      text-align: left;
      margin-top: 4%;
    }
  }
  &__logo{
    width: 124px;
  }
  h5{
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 1.4em;
    text-transform: uppercase;
    font-family: @font-medium;
  }
}
.social{
  display: flex;
  flex-direction: row;
}
.authors-bar{
  margin-top: 3%;
  background: #F2F2F2;
  p{
    font-family:@font_padrao;
    margin: 10px 0 0 0;
    font-weight: 700;
    padding-bottom: 20px;
    font-size: 1.9em;
  }
  strong{
    font-weight: 800;
    font-size: 1.3EM;
  }
}
.menu-popout{
  height: 100vh;
  width: 100vw;
  position: fixed;
  box-sizing: border-box;
  overflow: hidden;
  top: 0;
  display: none;
  transition: all 1s;
  .nav{
    display: block;
    margin: auto;
    height: 100%;
    ul{
      margin-top: 0;
      justify-content: space-evenly;
      height: 100%;
      flex-direction: column;
      font-size: 30px;
      li>a{
        font-family: @font_padrao;
        font-size: 3rem;
      }
    }
  }
  
}
@keyframes menu {
  0%{
    opacity: 0;
    background-color: #ffffff49;
  }
  100%{
    background-color: #ffffffcf;
    opacity: 1;
  }
}
div.appear-menu{
  display: block;
  animation: menu 700ms;
  animation-fill-mode: forwards;
}
